import PdfViewer from "@/app/recall/PdfViewer";

export default function LessonView({ subject, lesson }: { subject: string; lesson: string }) {
  // Generate file path
  const file = `/${subject.toLowerCase().replace(/[^a-z]/g, "")}-lesson${lesson.replace(/[^0-9]/g, "")}.pdf`;
  return (
    <div className="p-4 space-y-4">
      <h2 className="text-xl font-semibold text-cyan-500">{subject} / {lesson}</h2>
      <PdfViewer file={file} />
    </div>
  );
} 