"use client";
import { useState, useRef, useEffect } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import "react-pdf/dist/Page/AnnotationLayer.css";
import "react-pdf/dist/Page/TextLayer.css";
import React from "react";

// Configure pdfjs worker
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

export default function PdfViewer({ file = "/notespdf.pdf" }: { file?: string }) {
  // State
  const [numPages, setNumPages] = useState<number>(0);
  const [pageNumber, setPageNumber] = useState<number>(1);
  const [scale, setScale] = useState<number>(1);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [theme, setTheme] = useState<string>(() => typeof window !== "undefined" ? localStorage.getItem("pdfTheme") || "dark" : "dark");
  const [searchTerm, setSearchTerm] = useState("");
  const [searchMatches, setSearchMatches] = useState<{page: number, indices: number[]}[]>([]);
  const [searching, setSearching] = useState(false);
  const [thumbnails, setThumbnails] = useState<string[]>([]);
  const viewerRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);
  const [outline, setOutline] = useState<any[]>([]);
  const [showToc, setShowToc] = useState(false);
  const pageInputRef = useRef<HTMLInputElement>(null);

  // Fullscreen toggle
  const toggleFullscreen = () => {
    if (!document.fullscreenElement) {
      viewerRef.current?.requestFullscreen();
      setIsFullscreen(true);
    } else {
      document.exitFullscreen();
      setIsFullscreen(false);
    }
  };

  // Zoom controls
  const zoomIn = () => setScale((z) => Math.min(z + 0.2, 2));
  const zoomOut = () => setScale((z) => Math.max(z - 0.2, 0.5));

  // Page navigation
  const prevPage = () => setPageNumber((p) => Math.max(p - 1, 1));
  const nextPage = () => setPageNumber((p) => Math.min(p + 1, numPages));

  // Theme toggle
  const toggleTheme = () => {
    const newTheme = theme === "dark" ? "light" : "dark";
    setTheme(newTheme);
    if (typeof window !== "undefined") localStorage.setItem("pdfTheme", newTheme);
  };

  // Search logic (basic, highlight on current page)
  useEffect(() => {
    setSearchMatches([]);
    if (!searchTerm || !numPages) return;
    setSearching(true);
    // react-pdf does not provide direct search, so we use pdfjs directly
    (async () => {
      const loadingTask = pdfjs.getDocument(file);
      const pdf = await loadingTask.promise;
      const matches: {page: number, indices: number[]}[] = [];
      for (let i = 1; i <= pdf.numPages; i++) {
        const page = await pdf.getPage(i);
        const textContent = await page.getTextContent();
        const text = textContent.items.map((item: any) => item.str).join(" ");
        const indices: number[] = [];
        let idx = text.toLowerCase().indexOf(searchTerm.toLowerCase());
        while (idx !== -1) {
          indices.push(idx);
          idx = text.toLowerCase().indexOf(searchTerm.toLowerCase(), idx + 1);
        }
        if (indices.length > 0) matches.push({ page: i, indices });
      }
      setSearchMatches(matches);
      if (matches.length > 0) setPageNumber(matches[0].page);
      setSearching(false);
    })();
    // eslint-disable-next-line
  }, [searchTerm, file, numPages]);

  // Thumbnails generation
  useEffect(() => {
    if (!numPages) return;
    (async () => {
      const loadingTask = pdfjs.getDocument(file);
      const pdf = await loadingTask.promise;
      const thumbs: string[] = [];
      for (let i = 1; i <= pdf.numPages; i++) {
        const page = await pdf.getPage(i);
        const viewport = page.getViewport({ scale: 0.15 });
        const canvas = document.createElement("canvas");
        canvas.width = viewport.width;
        canvas.height = viewport.height;
        const context = canvas.getContext("2d");
        await page.render({ canvasContext: context!, viewport }).promise;
        thumbs.push(canvas.toDataURL());
      }
      setThumbnails(thumbs);
    })();
    // eslint-disable-next-line
  }, [file, numPages]);

  // Get outline/bookmarks on PDF load
  const onDocumentLoadSuccess = async ({ numPages, pdf }: any) => {
    setNumPages(numPages);
    if (pdf.getOutline) {
      const outlineData = await pdf.getOutline();
      setOutline(outlineData || []);
    } else {
      setOutline([]);
    }
  };

  // Tailwind theme classes
  const bgClass = theme === "dark" ? "bg-[#0f172a] text-white" : "bg-white text-gray-900";
  const toolbarClass = theme === "dark" ? "bg-[#1e293b] text-white" : "bg-gray-100 text-gray-900";
  const contentClass = theme === "dark" ? "bg-[#181f2a]" : "bg-gray-50";
  const borderClass = theme === "dark" ? "border-blue-900" : "border-blue-200";

  return (
    <div ref={viewerRef} className={`flex h-[80vh] md:h-[90vh] w-full rounded-xl shadow-xl overflow-hidden ${bgClass}`}> 
      {/* TOC Sidebar */}
      {outline.length > 0 && showToc && (
        <div className={`h-full w-56 overflow-y-auto border-r ${borderClass} bg-[#181f2a] flex flex-col items-start py-2 px-2 z-10`}>
          <div className="font-bold text-cyan-400 mb-2 flex items-center justify-between w-full">
            <span>Table of Contents</span>
            <button className="ml-auto text-xs text-gray-400 hover:text-cyan-400" onClick={() => setShowToc(false)}>‚úï</button>
          </div>
          <TocList outline={outline} setPageNumber={setPageNumber} />
        </div>
      )}
      {/* Sidebar Thumbnails */}
      <div className={`h-full w-16 md:w-20 overflow-y-auto border-r ${borderClass} bg-[#101828] flex flex-col items-center py-2`}>
        {outline.length > 0 && (
          <button
            className="mb-2 p-1 rounded bg-cyan-700 text-white hover:bg-cyan-600 text-xs w-12"
            onClick={() => setShowToc((v) => !v)}
            aria-label="Show Table of Contents"
          >
            TOC
          </button>
        )}
        {thumbnails.map((thumb, idx) => (
          <button
            key={idx}
            className={`mb-2 rounded-lg overflow-hidden border-2 ${pageNumber === idx + 1 ? "border-cyan-400" : "border-transparent"}`}
            onClick={() => setPageNumber(idx + 1)}
            aria-label={`Go to page ${idx + 1}`}
          >
            <img src={thumb} alt={`Page ${idx + 1}`} className="w-12 h-16 object-cover" />
          </button>
        ))}
      </div>
      {/* Main PDF Area */}
      <div className="flex-1 flex flex-col h-full">
        {/* Top Controls: Theme and Search only */}
        <div className={`flex items-center justify-between px-4 py-2 ${toolbarClass} border-b ${borderClass}`}>
          <div className="flex items-center gap-2">
            <button
              onClick={toggleTheme}
              className="ml-2 px-2 py-1 rounded bg-gray-700 text-white hover:bg-gray-600 text-xs"
              aria-label="Toggle dark/light mode"
            >
              {theme === "dark" ? "üåô" : "‚òÄÔ∏è"}
            </button>
          </div>
          <div className="flex items-center gap-2">
            <input
              type="text"
              placeholder="Search..."
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
              className="px-2 py-1 rounded bg-[#222] text-white text-xs focus:outline-none focus:ring focus:ring-cyan-400"
              style={{ minWidth: 120 }}
              aria-label="Search PDF"
            />
          </div>
        </div>
        {/* PDF Toolbar */}
        <div className={`w-full flex justify-between items-center px-4 py-2 ${toolbarClass} border-b ${borderClass}`}>
          <div className="flex items-center gap-3 text-sm">
            <button onClick={prevPage} className="hover:text-cyan-400" aria-label="Previous page">‚¨ÖÔ∏è Prev</button>
            <span className="opacity-80">Page {pageNumber} / {numPages || "..."}</span>
            <button onClick={nextPage} className="hover:text-cyan-400" aria-label="Next page">Next ‚û°Ô∏è</button>
          </div>
          <div className="flex items-center gap-3 text-sm">
            <button onClick={zoomOut} className="hover:text-cyan-400" aria-label="Zoom out">üîç‚ûñ</button>
            <button onClick={zoomIn} className="hover:text-cyan-400" aria-label="Zoom in">üîç‚ûï</button>
            <button onClick={toggleFullscreen} className="hover:text-cyan-400" aria-label="Toggle fullscreen">
              {isFullscreen ? "‚õ∂ Exit Fullscreen" : "‚õ∂ Fullscreen"}
            </button>
          </div>
        </div>
        {/* PDF Content */}
        <div className={`flex-1 flex justify-center items-center ${contentClass} overflow-auto`}>
          <Document
            file={file}
            onLoadSuccess={onDocumentLoadSuccess}
            loading={<div className="text-gray-400 p-8">Loading PDF...</div>}
            className="w-full flex justify-center"
          >
            <Page
              pageNumber={pageNumber}
              scale={scale}
              renderTextLayer={true}
              renderAnnotationLayer={false}
              className="flex justify-center"
              customTextRenderer={((props: { str: string; itemIndex: number }) => {
                const { str } = props;
                if (!searchTerm) return str;
                // Highlight search matches on current page
                const match = searchMatches.find(m => m.page === pageNumber);
                if (!match) return str;
                const regex = new RegExp(searchTerm, "gi");
                return str.split(regex).reduce((acc: React.ReactNode[], part, i, arr) => {
                  if (i < arr.length - 1) {
                    acc.push(part, <mark key={i} className="bg-yellow-300 text-black rounded px-1">{searchTerm}</mark>);
                  } else {
                    acc.push(part);
                  }
                  return acc;
                }, []);
              }) as any}
            />
          </Document>
        </div>
      </div>
    </div>
  );
}

// Table of Contents recursive list
function TocList({ outline, setPageNumber }: { outline: any[]; setPageNumber: (n: number) => void }) {
  if (!outline || outline.length === 0) return null;
  return (
    <ul className="pl-2">
      {outline.map((item, idx) => (
        <li key={idx} className="mb-1">
          <button
            className="text-cyan-300 hover:text-cyan-400 text-left text-xs mb-1"
            onClick={async () => {
              if (item.dest && typeof item.dest === "object" && item.dest[0]) {
                // Try to get page number from dest
                const ref = item.dest[0];
                if (ref && ref.num) setPageNumber(ref.num);
              } else if (item.pageNumber) {
                setPageNumber(item.pageNumber);
              }
            }}
          >
            {item.title}
          </button>
          {item.items && item.items.length > 0 && (
            <TocList outline={item.items} setPageNumber={setPageNumber} />
          )}
        </li>
      ))}
    </ul>
  );
}
